import { PropType, VNode } from 'vue';
import {
  MessageType,
  supportMessageTypes
} from './token';

export const messageProps = {
  type: { // 
    type: String as PropType<MessageType>,
    default: 'info',
    validator (typeVal: MessageType) {
      return supportMessageTypes.includes(typeVal);
    }
  },
  id: {
    type: String,
    default: null
  },
  message: { // vnodevnode
    type: [String, Object, Function] as PropType<string|VNode|(() => VNode)>,
    default: ''
  },
  icon: { // vnodevnode
    type: [String, Object, Function] as PropType<string|VNode|(() => VNode)>,
    default: ''
  },
  dangerouslyUseHTMLString: { //  message  HTML 
    type: Boolean,
    default: false
  },
  customClass: { // 
    type: String,
    default: ''
  },
  duration: { //   0 
    type: Number,
    default: 3000
  },
  showClose: { // 
    type: Boolean,
    default: true
  },
  showIcon: { // 
    type: Boolean,
    default: true
  },
  onClose: { // ,  message 
    type: Function,
    default: null
  },
  zIndex: {
    type: [Number, String],
    default: ''
  },
  offsetTop: { // Message 
    type: Number,
    default: 20
  },
  appendTo: { // document.body
    type: [String, HTMLElement],
    default: ''
  },
  grouping: { //  VNode 
    type: Boolean,
    default: false
  },
  repeatNum: { // 
    type: Number,
    default: 1
  }
};
