import {
  ref,
  computed
} from 'vue';

let zIndex = ref(0);
export function useZIndex () {
  let initiallyZIndex = 2000; // z-index
  let currentZIndex = computed(function () {
    return initiallyZIndex + zIndex.value;
  });
  let nextZIndex = function () {
    zIndex.value++;
    return currentZIndex.value;
  };
  return {
    currentZIndex,
    nextZIndex
  };
};
